* {
  margin: 0px;
  padding: 0;
  box-sizing: border-box;
  max-width: 100%;
  overflow-x: hidden;
}

/* Ensure body and HTML fit the screen */
html, body {
  width: 100%;
  overflow-x: hidden;
}

/* Center align footer content properly */
.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px;
  width: 100%;
}

/* Adjust social media icons and navigation */
.header-icons {
  display: flex;
  justify-content: center;
  gap: 10px;
}

/* Ensure images scale correctly */
img {
  max-width: 100%;
  height: auto;
}
