/* Enhanced BookingPage.css */

.booking-page {
    padding: 50px 20px;
    text-align: center;
    max-width: 800px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
    background: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 10px #D4AF37;
    margin-top: 120px;
    margin-bottom: 150px;


}

.booking-page h2 {
    font-size: 32px;
    margin-bottom: 20px;
    color: #333;
}

.calendar-wrapper {
    margin-bottom: 30px;
    text-align: center;
}

.calendar-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.booking-form label {
    display: block;
    margin-bottom: 10px;
    font-weight: bold;
    color: #333;
}

.time-slot select,
.event-details input,
.event-details textarea,
.event-details select {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
    box-sizing: border-box;
}

.event-details textarea {
    resize: none;
    height: 100px;
}

/* General Button Styling */
.btn-primary {
    background: #D4AF37; /* Gold */
    color: #fff; /* White text */
    padding: 12px 20px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.2s;
}

/* Hover effect for primary buttons */
.btn-primary:hover {
    background: #b38e2e; /* Slightly darker gold */
    transform: scale(1.05); /* Slight zoom effect */
}

/* "Weiterlesen" button */
.btn-secondary {
    background: #fff; /* White background */
    color: #D4AF37; /* Gold text */
    border: 2px solid #D4AF37;
    padding: 12px 20px;
    border-radius: 5px;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.2s;
}

/* Hover effect for secondary buttons */
.btn-secondary:hover {
    background: #D4AF37;
    color: #fff;
    transform: scale(1.05);
}


@media (max-width: 768px) {
    .booking-page {
        padding: 30px 15px;
    }

    .btn-primary {
        font-size: 14px;
        padding: 10px 20px;
    }

    .event-details textarea {
        height: 80px;
    }
}