/* General Navbar */
.navbar {
    width: 100%;
    background: rgba(0, 0, 0, 0.8);
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999; /* Ensure it appears above everything */
}

/* Center container */
.navbar .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

/* Left and right navigation menus */
.nav-left ul,
.nav-right ul {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    gap: 15px;
}

.nav-left ul li a,
.nav-right ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
    transition: color 0.3s ease;
}

.nav-left ul li a:hover,
.nav-right ul li a:hover {
    color: #ff9900;
}

/* Social icons */
.social-icons {
    display: flex;
    gap: 10px;
}

.social-icons a {
    color: #fff;
    font-size: 20px;
    text-decoration: none;
}

.social-icons a:hover {
    color: #ff9000;
}

/* Hamburger menu */
.hamburger {
    display: none;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
}

.hamburger div {
    width: 30px;
    height: 3px;
    background: #fff;
    transition: 0.3s;
}

/* Fullscreen popup menu */
.mobile-menu-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.9); /* Dark background */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 99999; /* Highest priority */
    transition: opacity 0.3s ease-in-out;
}

/* Navigation Links */
.mobile-menu-popup ul {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: center;
}

.mobile-menu-popup ul li {
    margin: 20px 0;
}

.mobile-menu-popup ul li a {
    font-size: 22px;
    color: white;
    text-decoration: none;
    transition: color 0.3s;
}

.mobile-menu-popup ul li a:hover {
    color: #D4AF37;
}

/* Close button */
.mobile-menu-close {
    position: absolute;
    top: 20px;
    right: 30px;
    font-size: 30px;
    color: white;
    cursor: pointer;
}

/* Mobile Responsive Adjustments */
@media (max-width: 768px) {
    .nav-left ul,
    .nav-right ul {
        display: none;
    }

    .hamburger {
        display: flex;
    }
}
